// <script>
import {ButtonArea, Button,
  CellsTitle, CellsTips,
  Cells, Cell, RadioCell, CheckboxCell, SwitchCell, InputCell, SelectCell,
  CellInput,
  Toptips} from 'vue-weui';

const sampleImage = 'data:image/png;base64,';

import vcode from '../vcode.jpg';

export default {
  data() {
    return {
      radioPicked: 'One',
      sampleImage: sampleImage,
      checkboxChecked: ['Two'],
      switchOn: false,
      vcode: vcode,
      qqValue: '',
      vcodeValue: '',
      bankCardValue: '',
      vcodeValue2: '',
      commentsValue: '',
      toptipsCount: 2,
      cardValue: '',
      dateValue: '',
      datetimeValue: '',
      areaCodeOptions: ['+86', '+80', '+84', '+87'],
      areaCodeSelected: '+86',
      phoneValue: '',
      contactTypeOptions: ['', 'QQ', 'Email'],
      contactTypeSelected: '',
      nationOptions: ['', '', ''],
      nationSelected: ''
    }
  },

  components: {
    ButtonArea,
    'weui-button': Button,
    CellsTitle,
    CellsTips,
    Cells,
    Cell,
    RadioCell,
    CheckboxCell,
    SwitchCell,
    InputCell,
    SelectCell,
    CellInput,
    Toptips
  },

  ready() {
    const handler = setInterval(() => {
      if (this.toptipsCount === 1) {
        clearInterval(handler);
      }
      this.toptipsCount -= 1;
    }, 1000);
  }
}
// </script>




